.modalContainer {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.backgroundTint {
    width:  100%;
    background-color: rgba(50, 50, 50, 0.9);
}

.modal-content {
    color: white;
    width: 100%;
    height: 100%;
    align-items: center;
}

.modal-header {
    width: 100%;
}

.modal-title {
    width: 100%;
    text-align: center;
}

.btn-close {
    margin-left: 0;
}

.modal-body {
    width: 100%;
    overflow: auto;
    overflow-x: hidden;
}

.modalBody {
    width: 100%;
    padding: 1rem;
    padding-top: 0.3rem;
}

.modal-body h5 {
  text-align: center;
  text-transform: capitalize;
}

.modal-footer {
    width: 100%;
}

